/* Table of Contents: 
  1. General Styles 
  2. Header
  3. Home
  4. About
  5. Contact
  6. Shop
  7. Cart 
  8. Footer
*/

/*  1. General Styles */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  background-color: #ffb4a2;
  height: 100vh;
  position: relative;
}

nav,
section,
.blog-header {
  padding: 1.5em 2em;
  font-size: 1rem;
}

a {
  text-decoration: none;
  color: black;
}

p {
  white-space: pre-wrap;
}

.button {
  border: none;
  color: white;
  background-color: #ef476f;
  font-size: 1.1rem;
  cursor: pointer;
  padding: 1.3em;
  text-align: center;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: rgba(239, 71, 111, 0.5);
}

.bg-white {
  background-color: white;
  width: 100%;
}

.btn-lrg {
  width: 20%;
}

/* 2. Header */

.navbar {
  list-style-type: none;
  display: flex;
  align-items: center;
}

.title {
  margin-right: auto;
}

.title h2 {
  font-size: 1.8rem;
}

.link {
  margin-right: 1em;
}

.active {
  padding-bottom: 0.3rem;
  border-bottom: 2px solid black;
}

.logo {
  margin-right: 0.5em;
}

.logo img {
  height: 1.5rem;
  width: 1.5rem;
}

.cart-icon {
  margin-left: 0.5em;
  position: relative;
}

.cart-quantity-container {
  position: absolute;
  top: -1em;
  right: -0.3em;
  font-size: 1rem;
}

@media screen and (max-width: 650px) {
  .navbar {
    flex-wrap: wrap;
    gap: 2em;
    justify-content: center;
    text-align: center;
  }

  .navbar > * {
    flex: 1 1 100%;
    margin: initial;
    background-color: #ef476f;
    padding: 1.3em;
  }

  .cart-quantity-container {
    right: -1.3em;
    left: 0;
    top: 0.4em;
  }

  .logo {
    display: none;
  }
}

/*  End Header */

/*  3. Home */

/*  3.1. blog header */

.blog-header {
  display: flex;
  gap: 5em;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.blog-header > * {
  flex: 1 1 35.9rem;
}

.blog-description h3 {
  margin-bottom: 2em;
  font-size: clamp(1.1rem, 0.35rem + 1.6vw, 1.3rem);
}

.blog-description p {
  font-size: clamp(1.3rem, -0.2rem + 3.2vw, 1.7rem);
  font-weight: 700;
  max-width: 40ch;
}

@media screen and (max-width: 750px) {
  .blog-header {
    flex-wrap: wrap;
  }

  .blog-header > * {
    flex: 1 1 100%;
  }

  .blog-header-img {
    width: inherit;
  }

  .blog-description {
    text-align: center;
  }

  .blog-description p {
    margin: auto;
  }
}

/* 3.2. recent posts */

.intro {
  margin: auto;
  margin-top: 5em;
  margin-bottom: 5em;
  text-align: center;
  max-width: max-content;
}

.intro h3 {
  font-size: 1.8rem;
  margin-bottom: 1em;
  color: #ef476f;
}

.intro p {
  max-width: 75ch;
  font-size: 1.1rem;
  font-weight: 100;
  margin: auto;
}

.posts {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 2375px;
  align-items: center;
  width: 100%;
}

.post {
  width: 30%;
  display: flex;
  flex-direction: column;
  gap: 1.5em;
  margin: 0 1em;
  margin-bottom: 1.5em;
}

.post-img {
  width: 100%;
}

.post img {
  width: inherit;
}

.post-date {
  font-size: 1.3rem;
}

.post-name {
  font-size: 1.8rem;
  max-width: 20ch;
}

.post-name a {
  color: #ef476f;
}

.post p {
  max-width: 35ch;
}

.read-more {
  width: max-content;
  border-bottom: #ef476f solid 2px;
  color: #ef476f;
}

@media screen and (max-width: 800px) {
  .posts {
    height: max-content;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .post {
    flex: 1 1 100%;
  }

  .post-img img {
    height: inherit !important;
  }
}

/*  3.3. contact */

.update {
  background-image: url('./images/cake2.jpg');
  background-repeat: no-repeat;
  background-size: 95% 500px;
  background-position: center;
  height: 600px;
  display: grid;
  place-items: center;
  text-align: center;
  color: white;
}

.form-header > * {
  margin-bottom: 1.3em;
}

.form-header > h3 {
  font-size: 1.4rem;
}

.form-input > label {
  display: none;
}

.form-input input {
  padding: 1.3em;
  width: clamp(5em, 10vw + 10em, 30em);
  font-size: 1.1rem;
}

.submit {
  margin-left: 1em;
}

/*  End Home */

/*  4. About */

.about {
  display: flex;
  white-space: pre-wrap;
  gap: 2.5em;
}

.test h2 {
  font-size: clamp(1.3rem, -0.0333rem + 4.2667vw, 2.1rem);
  max-width: 35ch;
}

.test p {
  max-width: 65ch;
  font-size: clamp(1rem, -0.6rem + 3.2vw, 1.2rem);
}

.test > * {
  margin-bottom: 1.5em;
  line-height: 1.4em;
}

.test-img {
  height: clamp(20rem, -0.2rem + 64.64vw, 40.2rem);
  width: 40%;
}

@media screen and (max-width: 800px) {
  .about {
    flex-wrap: wrap;
  }

  .about > * {
    flex: 1 1 100%;
  }
}

/*  End About */

/*  5. Contact */

.contact {
  margin: auto;
  max-width: 50%;
}

/*  5.1. get in touch */

.get-in-touch {
  margin-top: 5em;
}

.get-in-touch h2 {
  font-size: clamp(0.1rem, 50vw, 2.5rem);
}

.get-in-touch p {
  font-size: 1.1rem;
  margin: 2.5em auto;
  line-height: 1.7em;
}

.contact-info {
  margin-bottom: 2.5em;
}

.contact-info h3 {
  font-size: 1.3rem;
}

.address-social .social {
  margin: initial;
}

.address-social .social a {
  margin-right: 1em;
}

.address-social .social img {
  height: 1.5rem;
  width: 1.5rem;
}

@media screen and (max-width: 850px) {
  .contact {
    margin: initial;
    max-width: 100%;
  }
}

/*  5.2. contact form */

.contact-form {
  margin-top: 5em;
  margin-bottom: 2em;
}

#contact-form {
  display: flex;
  gap: 1.5em;
  flex-direction: column;
  width: 100%;
}

#contact-form input {
  padding: 0.8em;
  border: none;
}

#contact-form .button {
  margin: initial;
  width: 8em;
}

.contact-form-input {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact-form-input > * {
  flex: 1 1 100%;
}

.full-name {
  display: flex;
  border: none;
  gap: 1.5em;
}

.name {
  font-size: 0.9rem;
}

#message {
  flex: 1 1 10em;
  resize: none;
  border: none;
}

/*  End Contact */

/*  6. Shop */

.shop-items {
  margin-top: 8em;
  padding: 1.5em 3em;
}

.item {
  display: flex;
  gap: 5em;
  height: max-content;
  margin-bottom: 5em;
  justify-content: center;
  width: 100%;
}

.item > * {
  flex: 1 1 100%;
}

.product-img,
.item-description {
  height: unset;
}

.product-img {
  max-width: 100%;
}

.item-description {
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.item-description h1 {
  font-size: clamp(1.3rem, 0.3667rem + 4.2667vw, 2.5rem);
}

.item-price-container .item-price {
  font-size: 1.3rem;
}

.item-details {
  line-height: 1.5em;
  font-size: clamp(1rem, 0.8444rem + 0.7111vw, 1.2rem);
  max-width: 45ch;
}

.item-quantity {
  display: flex;
  flex-direction: column;
}

.item-quantity-lable {
  font-size: 1.1rem;
}

#quantity {
  font-size: 1.2rem;
  padding: 1.4em;
  width: 8em;
  border: black 1px solid;
}

@media screen and (max-width: 800px) {
  .item {
    flex-wrap: wrap;
  }
}

/*  End Shop */

/*  7. Cart */

/*  7.1 cart empty */

.cart-empty {
  margin-top: 4em;
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.cart-empty h3 {
  font-size: 1.5rem;
}

.cart-empty p {
  font-size: 1.1rem;
}

/*  7.2 cart item */

.cart-item {
  display: flex;
  border-bottom: hsla(0, 0%, 39%, 0.2) solid 2px;
  padding-bottom: 2em;
  margin-bottom: 5em;
  position: relative;
}

.cart-item button {
  width: 1.2rem;
  height: 1.2rem;
  background-color: inherit;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
  border-radius: 50%;
}

.cart-item button:hover {
  background-color: white;
}

.item-img-container {
  width: clamp(4rem, -6.6667rem + 21.3333vw, 8rem);
  height: clamp(4rem, -6.6667rem + 21.3333vw, 8rem);
}

.item-img {
  width: inherit;
  height: inherit;
  background-repeat: no-repeat;
  background-size: cover;
}

.item-name {
  margin-right: auto;
  display: flex;
  gap: 2em;
}

.item-name h3 {
  max-width: clamp(5rem, -100rem + 480vw, 20rem);
  font-size: clamp(1rem, -0.5833rem + 6.6667vw, 1.5rem);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.increment-decrement {
  margin-right: 10em;
  width: 20ch;
  text-align: center;
}

.increment-decrement img {
  width: 1.2rem;
  height: 1.2rem;
}

.item-number {
  font-size: 1.2rem;
  margin: auto 1.3em;
}

.item-price {
  display: flex;
  gap: 1.5em;
  width: 15ch;
  font-size: 1.2rem;
}

@media screen and (max-width: 800px) {
  .increment-decrement {
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .item-full-price {
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .delete {
    margin-left: auto;
  }
}

/*  7.3 checkout */

.checkout {
  width: 25em;
  margin-left: auto;
  margin-right: 5em;
}

.subtotal {
  display: flex;
  width: inherit;
}

.subtotal > * {
  font-size: 1.4rem;
}

.full-price {
  margin-left: auto;
}

.checkout-btn {
  width: inherit;
  margin-top: 2em;
}

@media screen and (max-width: 950px) {
  .checkout {
    width: 100%;
    margin: initial;
  }
}

/*  End Cart */

/*  8. Footer */

footer {
  display: grid;
  place-items: center;
}

.social {
  margin-top: 2.5em;
}

.social a {
  margin-right: 1.5em;
}

.social img {
  height: 2rem;
  width: 2rem;
}

#cart + footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

/*  End Footer */
